<template>
	<div id='userManage'>
		<div class="top">
			<div class="topLeft">
				<el-input class="selectInput" v-model="huahaiNumber" placeholder="华海号"></el-input>

				<el-input class="selectInput" v-model="userName" placeholder="用户昵称"></el-input>

				<el-input class="selectInput" v-model="userId" placeholder="姓名"></el-input>

				<el-input class="selectInput" v-model="userId" placeholder="身份证号"></el-input>

				<el-input class="selectInput" v-model="userId" placeholder="企业名称"></el-input>

				<el-input class="selectInput" v-model="userId" placeholder="信用代码"></el-input>

				<el-input class="selectInput" v-model="userId" placeholder="电话号码"></el-input>

				<el-input class="selectInput" v-model="userId" placeholder="交易单号"></el-input>

				<el-button type="primary" class="selectInput"  >查询</el-button>
			</div>
		</div>

		<div class="bottom">
			<el-table
			:data="tableData"
			height='1030'
			border
			stripe
			lazy
			size="mini"
			row-key="id"
			:tree-props="{children: 'children', hasChildren: 'hasChildren'}">
				<el-table-column
					class="tableClass"
					align='center'
					prop="id"
					label="头像"
					width="150">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="name"
					label="昵称">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount1"
					label="认证状态">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount2"
					label="电话号码">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount3"
					sortable
					label="会员(是/否)">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount3"
					label="详情">
					<template slot-scope="scope">
						<el-button @click="handleClick(scope.row)" type="text" size="small">详细信息</el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>

		<div class="pagination">
			<pagination @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange" :total="251"></pagination>
		</div>
	</div>
</template>

<script>

import { regionData, CodeToText } from "element-china-area-data";
import pagination from "../../../components/pagination"
export default {
	name:'userManagement',
	components:{
		pagination
	},
	data(){
		return{
			dialog:'',
			huahaiNumber:'',
			userName:'',
			userId:'',
			phoneNumber:'',
			state:'',
			startTime:'',
			endTime:'',
			dialogFormVisible:false,
			areaSelectData:regionData,
			selectedOptions:[],
			selectCommand:'待审核',
			tableData: [
				{
					id: '12987123',
					name: '王小虎',
					amount1: '165',
					amount2: '4.43',
					amount3: 12
				},
				{
					id: '12987124',
					name: '王小虎',
					amount1: '324',
					amount2: '1.9',
					amount3: 9
				},
				{
					id: '12987125',
					name: '王小虎',
					amount1: '621',
					amount2: '2.2',
					amount3: 17
				}
			]
		}
	},

	created(){

	},

	methods:{
		handleClick(row){
			this.dialog = row;
			this.$router.push({
				path:"/userInfo",
				query:{
					activeName:'authenticationInfo',
				}
			})
		},
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
		},
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
		},

		handleChange() {
			let loc = "";
			for (let i = 0; i < this.selectedOptions.length; i++) {
				loc += CodeToText[this.selectedOptions[i]];
			}
		},

		handleCommand(command){
			this.selectCommand = command;
		},
	}
}
</script>

<style scoped>

	#userManage{
		overflow:auto;
		box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
		padding: 20px;
		margin-top: 20px;
	}

	.top{
		/* width: 1620px; */
		height:110px;
		display: flex;
		margin-top:-20px;
		justify-items: space-between;
	}

	.bottom{
		overflow: scroll;
	}

	.selectInput{
		width: 250px !important;
		margin-right: 30px;
		margin-top: 10px;
		height:40px
	}

	.pagination{
		width: 100%;
		display: flex;
		justify-content: center;
		margin-top: 10px;
	}

</style>
